:root {
	--primary-light-color: #f5f5f5;
	--primary-dark-color: #1a1b21;
	--secondary-light-color: #dcdcdc;
	--secondary-dark-color: #23252c;
	--tertiary-dark-color: #161619;
	--orange-accent-color: #e29261;
	--blue-accent-color: #5093e2;
}

* {
	box-sizing: border-box;
	margin: 0;
}

body {
	font-family: "Open Sans", sans-serif;
	background-color: var(--tertiary-dark-color);
}

.cards {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 50px;
	padding: 50px;
}

@media screen and (max-width: 480px) {
	.cards {
		padding: 0;
	}
}

.container {
	width: 550px;
	background-color: var(--secondary-dark-color);
	padding: 45px;
}

/* insert a (collapsed) flex row to force a line break */
.break-row {
	flex-basis: 100%;
	height: 0;
}

.card-1,
.card-2,
.card-3,
.card-4 {
	width: 320px;
	border-radius: 10px;
	margin: auto;
}

.card-1,
.card-2 {
	color: var(--primary-light-color);
	background-color: var(--primary-dark-color);
}

.card-3,
.card-4 {
	color: var(--primary-dark-color);
	background-color: var(--primary-light-color);
}

/* Info */
/********/
.info {
	text-align: center;
}

.info--photo {
	width: 20rem;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	background-clip: padding-box;
}

.info > h2 {
	margin-top: 20px;
}

.info > h5 {
	color: var(--orange-accent-color);
	font-weight: 400;
}

.info > h6 {
	margin-top: 9px;
	font-weight: 400;
}

.info--buttons {
	display: flex;
	justify-content: space-between;
	padding: 0 35px;
}

.info--button--email,
.info--button--linkedin {
	font-size: 14px;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	margin-top: 15px;
	padding: 9px 13px 9px 11px;
	width: 115px;
}

.card-2 .info--button--email,
.card-4 .info--button--email {
	width: 100%;
}

button > i {
	margin-right: 8px;
}

.info--button--email {
	background-color: var(--primary-light-color);
	color: var(--primary-dark-color);
}

.card-3 .info--button--email,
.card-4 .info--button--email {
	background-color: #ffff;
	border: 1px solid var(--secondary-light-color);
	box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
}

.info--button--email:hover {
	color: var(--primary-light-color);
	background-color: var(--orange-accent-color);
}

.info--button--linkedin {
	background-color: var(--blue-accent-color);
	color: var(--primary-light-color);
}

.info--button--linkedin:hover {
	background-color: hsl(212, 72%, 50%);
}

/* About & Interests */
/*********************/
.about,
.interests {
	margin-top: 30px;
	padding: 0 35px;
}

.interests {
	margin-top: 16px;
}

p {
	font-size: 10.5px;
	color: var(--secondary-light-color);
	line-height: 1.5;
	margin-top: 5px;
}

.card-3 p,
.card-4 p {
	color: var(--secondary-dark-color);
}

/* Footer */
/**********/
.socials {
	background-color: var(--tertiary-dark-color);
	margin-top: 43px;
	padding: 20px 73px;
	display: flex;
	justify-content: space-between;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
}

.card-2 .socials,
.card-4 .socials {
	padding: 20px 50px;
}

.card-3 .socials,
.card-4 .socials {
	background-color: var(--secondary-light-color);
}

.socials img {
	cursor: pointer;
}
